export interface StoryMetadata {
    pluginName: string;
    storyTitle: string;
    storyID: string;
    scenes: Scene[];
    alexaVoiceModel?: any;
}

export interface Scene {
    readonly id: string;
    readonly contents: SceneVariation[];
    readonly customProperties?: {[key:string]: any}
}

export interface SceneVariation {
    condition?: string;
    narration?: string;
    sceneDirections?: Instruction[];
}

export interface Instruction {
    readonly directionType: InstructionType,
    readonly parameters: {[key: string]: any;},
    sourcePosition?: number,
    sourceScene?: string
}

export enum BuiltInScenes {
    ResumeScene= "Resume",
    PauseScene= "Pause",
    StartScene= "Start"
}

export enum InstructionType {
    CONDITION = "condition",
    CHOICE = "choice",
    BGM = "backgroundmusic",
    INCREASE = "increase",
    REDUCE = "reduce",
    MULTIPLY = "multiply",
    DIVIDE = "divide",
    MODULUS = "modulus",
    SET = "set",
    FLAG = "flag",
    UNFLAG = "unflag",
    CLEAR = "clear",
    RECAP = "recap",
    ROLL = "roll dice",
    ADD_TO_INVENTORY = "add inventory",
    ADD_ITEM = "add item",
    REMOVE_ITEM = "remove item",
    REMOVE_FIRST = "remove first",
    REMOVE_LAST = "remove last",
    ADD_COLLECTION = "add collection",
    CUSTOM = "custom",
    REPROMPT = "reprompt",
    GET_TIME = "get time",
    VISUALS = "visuals",
    SLOT = "slot",
    BOOKMARK = "bookmark",

    GO_TO = "go to",
    SAVE_AND_GO = "save and go",

    END = "ending",
    RESTART = "restart",
    RESUME = "resume",
    RETURN = "return",
    REPEAT = "repeat",
    BACK = "back",
    PAUSE = "pause",
    REPEAT_REPROMPT = "repeat reprompt"
}